#topButton{visibility:hidden;position:fixed;bottom:20px;right:30px;z-index:99;border:none;outline:none;background-color:#000;color:#fff;cursor:pointer;padding:0;border-radius:15px;font-size:35px;display:flex;align-items:center;justify-content:center;width:50px;height:50px;line-height:1}#topButton::before{content:"⌃";display:block;line-height:1;transform:translateY(6px)}#topButton:hover{background-color:rgba(0,0,0,.6);transition:all ease-in-out .2s}